<template>
	<v-ace-editor :readOnly='readOnly' :options="editOption" v-model:value="dataEdit" :lang="lang" :theme="theme" :style="{height: height}" />
	<el-button type="primary" size="small" @click='formatJson' v-if="lang==='json'">json</el-button>
</template>

<script>
import { VAceEditor } from 'vue3-ace-editor';
import 'ace-builds/src-noconflict/snippets/json';
import 'ace-builds/src-noconflict/mode-json';
import 'ace-builds/src-noconflict/snippets/python';
import 'ace-builds/src-noconflict/mode-html';
import 'ace-builds/src-noconflict/snippets/html';
import 'ace-builds/src-noconflict/mode-python';
import 'ace-builds/src-noconflict/theme-chrome';
import 'ace-builds/src-noconflict/theme-monokai';
import 'ace-builds/src-noconflict/ext-language_tools';
import ace from 'ace-builds';
ace.config.set('basePath', 'https://cdn.jsdelivr.net/npm/ace-builds@' + require('ace-builds').version + '/src-noconflict/');
import JSON5 from 'json5';
export default {
	components: {
		VAceEditor
	},
	methods:{
formatJson() {
  try {
    //  JSON5  JSON.parse JSON
    const jsObj = JSON5.parse(this.dataEdit);

    //  JSON 
    this.dataEdit = JSON.stringify(jsObj, null, 4);

    this.$message({
      message: 'JSON ',
      type: 'success'
    });
  } catch (e) {
    this.$message.error('JSON ');
  }
}
	},
	props: {
		lang: {
			default: 'json'
		},
		modelValue: {
			type: String,
		},
		theme:{
			default:'chrome'
		},
		height:{
			default:"300px"
		},
		readOnly:{
			default: false
		},

	},
	emits: ['update:modelValue'],
	computed: {
		editOption() {
			return {
				enableBasicAutocompletion: true, // 
				enableSnippets: true, // 
				enableLiveAutocompletion: true, // 
				tabSize: 4, // 	tab
				fontSize: 18, // 
				useWorker: false, // json
				showPrintMargin: false, //
				enableMultiselect: true, // 
				// readOnly: true, // 
				showFoldWidgets: true, // 
				fadeFoldWidgets: true, // 
				wrap: true //
			};
		},
		dataEdit: {
			get() {
				return this.modelValue;
			},
			set(value) {
				this.$emit('update:modelValue', value);
			}
		}
	}
};
</script>

